.md-dialog-actions{min-height:52px;padding:8px 8px 8px 24px;display:flex;align-items:center;justify-content:flex-end;position:relative}.md-dialog-actions::before{height:1px;position:absolute;top:-1px;right:0;left:0;content:" "}.md-dialog-actions .md-button{min-width:64px;margin:0}.md-dialog-actions .md-button+.md-button{margin-left:8px}.md-dialog-content{padding:0 24px 24px;flex:1;flex-basis:auto;overflow:auto;position:relative}.md-dialog-content:first-child{padding-top:24px}.md-dialog-content p:first-child:not(:only-child){margin-top:0}.md-dialog-content p:last-child:not(:only-child){margin-bottom:0}.md-dialog-content.md-dialog-prompt-content>.md-field{margin:0}.md-dialog-title{margin-bottom:20px;padding:24px 24px 0}.md-dialog{background-color:var(--md-elevation-24);box-shadow:0 11px 15px -7px rgba(0,0,0,.2),0 24px 38px 3px rgba(0,0,0,.14),0 9px 46px 8px rgba(0,0,0,.12);min-width:280px;max-width:80%;max-height:80%;margin:auto;display:flex;flex-flow:column;flex-direction:row;overflow:hidden;position:fixed;top:50%;left:50%;z-index:110;border-radius:2px;backface-visibility:hidden;pointer-events:auto;transform:translate(-50%,-50%);transform-origin:center center}.md-dialog-enter,.md-dialog-leave{transition:opacity .15s cubic-bezier(.25, .8, .25, 1),transform .2s cubic-bezier(.25, .8, .25, 1)}.md-dialog-enter>.md-dialog-actions,.md-dialog-enter>.md-dialog-content,.md-dialog-enter>.md-dialog-tabs,.md-dialog-enter>.md-dialog-title,.md-dialog-leave>.md-dialog-actions,.md-dialog-leave>.md-dialog-content,.md-dialog-leave>.md-dialog-tabs,.md-dialog-leave>.md-dialog-title{transition:opacity .3s cubic-bezier(.4, 0, .2, 1),transform .25s cubic-bezier(.4, 0, .2, 1)}.md-dialog-enter,.md-dialog-leave-active{opacity:0;transform:translate(-50%,-50%) scale(.9)}.md-dialog-enter>.md-dialog-actions,.md-dialog-enter>.md-dialog-content,.md-dialog-enter>.md-dialog-tabs,.md-dialog-enter>.md-dialog-title,.md-dialog-leave-active>.md-dialog-actions,.md-dialog-leave-active>.md-dialog-content,.md-